.hello{
  display: flex;
  flex-direction: column;
 margin-top: 500px; 
 
}


.name{
  color: #E1DAD2;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: Roboto;
  font-size: 200px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 9.6px;
  text-transform: uppercase;
  z-index: 5;
  margin: 0;
  animation: slideUp 2s cubic-bezier(0.65, 0, 0.35, 1);
  animation-fill-mode: forwards;
 
}
@keyframes slideUp {
  0% {transform: translateY(0);opacity:0;}
  100% {transform: translateY(-250px);opacity:1;}


}
.name_outline{
text-align: center;

font-family: Roboto;
font-size: 200px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 9.6px;
text-transform: uppercase;
z-index: 1;
margin: 0;
-webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
-webkit-text-stroke-width: 5px;
-webkit-text-stroke-color:#E1DAD2;
animation: slideUp 2s cubic-bezier(0.65, 0, 0.35, 1);
animation-fill-mode: forwards;

}


@media only screen and (max-width: 414px) {
  .hello{
    display: flex;
    flex-direction: column;
   margin-top: 201px; 
  }
  
  
  .name{
    color: #E1DAD2;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: Roboto;
    font-size: 50px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 9.6px;
    text-transform: uppercase;
    z-index: 5;
    margin: 0;
    animation: slideUp 2s cubic-bezier(0.65, 0, 0.35, 1);
    animation-fill-mode: forwards;
  }
  @keyframes slideUp {
    0% {transform: translateY(0);opacity:0;}
    100% {transform: translateY(-100px);opacity:1;}
  
  
  }
  .name_outline{
  text-align: center;
  
  font-family: Roboto;
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 9.6px;
  text-transform: uppercase;
  z-index: 1;
  margin: 0;
  -webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color:#E1DAD2;
  animation: slideUp 2s cubic-bezier(0.65, 0, 0.35, 1);
  animation-fill-mode: forwards;
  
  }
}